 expand-out 1s;\n    animation-delay: 0.5s;\n    animation-fill-mode: both;\n  }\n  .footer {\n    display: flex;\n    position: relative;\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n    gap: 2px;\n    margin: 0px;\n    animation: 1s slideInFromBottom;\n    animation-delay: 1s;\n    animation-fill-mode: both;\n    div {\n      display: flex;\n      justify-content: center;\n      align-items: center;\n      flex-direction: column;\n      border-radius: 10px;\n      padding: 5px;\n      transition: all 0.2s ease-in-out;\n    }\n    .innerFooter {\n      padding: 0;\n      margin: 0;\n      width: 100%;\n      display: flex;\n      justify-content: space-around;\n      flex-direction: row;\n    }\n  }\n\n  a {\n    text-decoration: underline;\n    color: white;\n    font-size: 1.5rem;\n    margin: 0;\n    padding: 0;\n    border-radius: 10px;\n    transition: all 0.2s ease-in-out;\n    width: fit-content;\n    height: fit-content;\n    &:hover {\n      background-color: rgba(95, 95, 95, 0.232);\n      transform: scale(1.1);\n      color: #52baff;\n    }\n  }\n\n  .innerLogin {\n    display: flex;\n    position: relative;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n    padding: 0;\n    margin: 0;\n    border: none;\n    border-radius: 10px;\n    overflow: auto;\n  }\n\n  .header {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    font-size: 1.5rem;\n    margin: 0px;\n    padding: 0px;\n    width: 100%;\n    color: white;\n    background-color: $glass-blue;\n    border-bottom: 2px solid white;\n  }\n  .outerForm {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n  }\n  .formContainer {\n    display: flex;\n    flex-direction: column;\n    // justify-content: center;\n    align-items: center;\n    overflow: hidde